.movieCard {
    border-radius: 4px;
    background-color: #fff;
    overflow: hidden;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.movieCard .content {
    padding: 8px;
}

.movieCard img {
    display: block;
    max-width: 100%;
}

.movieCard .title {
    margin-top: 0;
    margin-bottom: 0;
    font-weight: 500;
}

.movieCard .descr {
    margin-bottom: 0;
    line-height: 1.5;
}